<template>
  <div class="work-list">
    <div class="work-head">
      <el-button icon="el-icon-circle-plus-outline"
                 class="create-produc"
                 @click="createProducDialogVisible=true">新建作品</el-button>
    </div>
    <div class="list-content">
      <work-list-item v-for="(item, index) in listData"
                      :key="item.name+index"
                      :item="item"
                      :coverClick="_itemClick"
                      :writingClick="_goWriting"
                      :havaReleasedClick="_releasedList"
                      :editClick="_edit">
      </work-list-item>
    </div>

    <el-dialog class="create-produc-dialog"
               title="创建作品"
               center
               :show-close="false"
               :close-on-click-modal="false"
               :visible.sync="createProducDialogVisible"
               width="350px">

      <create-produc ref="addProduc"
                     :addSuccessHandle="_createProducSuccess"></create-produc>
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="createProducDialogVisible = false">取 消</el-button>
        <el-button type="primary"
                   @click="_createProduc">确 定</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
import { producData } from "@/../mock/home_mock"
import WorkListItem from "@/components/WorkListItem.vue"
import CreateProduc from "@/components/CreateProduc.vue"

export default {
  name: "WorkList",
  components: { WorkListItem, CreateProduc },
  data () {
    return {
      listData: producData,
      createProducDialogVisible: false
    }
  },
  methods: {
    _itemClick () {
      const path = this.$router.resolve("/detail");
      window.open(path.href, "_blank")
    },
    _goWriting () {
      const path = this.$router.resolve({ name: 'writ', params: { type: 'addType' } })
      window.open(path.href, "_blank")
    },
    _releasedList () {
      const path = this.$router.resolve({ name: 'writ', params: { type: 'released' } })
      window.open(path.href, "_blank")
    },
    _edit () {

    },
    _createProduc () {
      let el = this.$refs.addProduc
      el.upload()
    },
    _createProducSuccess (produce) {
      this.createProducDialogVisible = false
      this.listData = [...this.listData, produce]
    },
  }
}
</script>

<style lang="less" scoped>
.work-list {
  .work-head {
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
    height: 42px;
    width: 100%;
    padding: 5px 0;
    box-sizing: border-box;
    .create-produc {
      padding: 0 8px;
      margin-right: 5px;
      height: 32px;
      border: none;
      float: right;
      font-size: 15px;
      background-color: #409eff30;
      color: #409eff;
    }
  }
  .list-content {
    width: 100%;
    height: 100%;
  }
}
</style>