<template>
  <div class="work-list-item"
       @click="_click">
    <div class="cover-container">
      <el-image :src="item.cover"
                :class="coverimg_class"
                ref="coverimg"
                @click="_imgClick"
                @mouseover="_coveimg_onmouseenter"
                @mouseleave="_coverimg_onmouseleave"></el-image>
    </div>

    <div class="info">
      <p>{{item.name}}</p>
      <p>{{'最新章节: '+ item.lastChapter}}</p>
      <p>{{'更新时间: ' + item.updateTime}}</p>
      <p>{{'创建时间: ' + item.createTime}}</p>
      <p>{{'收藏: ' + item.collect}}</p>
    </div>
    <div class="opertion">
      <el-button type="primary"
                 plain
                 @click="_writing">去写作</el-button>
      <el-button type="success"
                 plain
                 @click="_havaReleased">已发布</el-button>
      <el-button @click="_edit">编 辑</el-button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    item: {},
    coverClick: Function,
    writingClick: Function,
    havaReleasedClick: Function,
    editClick: Function
  },
  data () {
    return {
      coverimg_class: [""],
      is_mouseleave: true
    }
  },
  methods: {
    _click () {
      this.is_mouseleave = true
      this.$emit("click", this.item);
    },
    _imgClick () {
      this.is_mouseleave = true
      this._resetCoverimgAnima()
      if (this.coverClick) this.coverClick(this.item)
    },
    _writing () {
      if (this.writingClick) this.writingClick(this.item)
    },
    _havaReleased () {
      if (this.havaReleasedClick) this.havaReleasedClick(this.item)
    },
    _edit () {
      if (this.editClick) this.editClick(this.item)
    },
    _coveimg_onmouseenter () {
      this.is_mouseleave = false
      let el_coverimg = this.$refs.coverimg.$el;
      el_coverimg.style.animationFillMode = "forwards"
      this.coverimg_class = ["coverimg_mouseenter"];
    },
    _coverimg_onmouseleave () {
      if (this.is_mouseleave) return
      let el_coverimg = this.$refs.coverimg.$el;
      el_coverimg.style.animationFillMode = "backwards"
      this.coverimg_class = ["coverimg_mouseleave"];
      this.is_mouseleave = true
    },
    _resetCoverimgAnima () {
      this.is_mouseleave = true
      let el_coverimg = this.$refs.coverimg.$el;
      el_coverimg.style.animationFillMode = "backwards"
      this.coverimg_class = [""];
      el_coverimg.style.animationName = ""
    }
  },
  deactivated () {
    this._resetCoverimgAnima()
  },

}
</script>

<style lang="less" scoped>
@keyframes imgMouseenterAnima {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.2);
  }
}

@keyframes imgMouseleaveAnima {
  0% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

.coverimg_mouseenter {
  animation-name: imgMouseenterAnima;
}

.coverimg_mouseleave {
  animation-name: imgMouseleaveAnima;
}

.work-list-item {
  box-sizing: border-box;
  margin: 10px 10px;
  padding-bottom: 10px;
  border-bottom: solid 1px rgba(0, 0, 0, 0.05);
  width: calc(100% - 20px);
  height: 136px;
  position: relative;
  .cover-container {
    float: left;
    width: 100px;
    height: 100%;
    display: block;
    overflow: hidden;
    border-radius: 2px;
    .el-image {
      width: 100px;
      height: 100%;
      animation-duration: 0.25s;
      animation-timing-function: ease-in-out;
    }
  }
  .info {
    display: flex;
    float: left;
    margin-left: 10px;
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
    p {
      font-size: 12px;
      text-align: left;
      margin: 0;
    }
    p:first-child {
      font-size: 14px;
      font-weight: 500;
    }
  }
  .opertion {
    float: right;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    .el-button {
      display: block;
      margin: 0;
      padding: 8px;
    }
  }
}
</style>